<template >
    <div>
        <v-row class="ma-5" >

                <v-col cols="12" xs="12" sm="12" md="12">
                    <v-card v-model="fecha" color="blue" elevation="4">
                        <h2 class="display-1 text-center pt-5">USD a CLP</h2>
                        <p class="text-center">(Selecciona una fecha)</p>
                        <h2 class="display-1 text-center py-5">{{valor}}</h2>
                    </v-card>
                </v-col>

                
                <v-row justify="center">
                    <v-date-picker 
                    v-model="fecha" 
                    :min="minimo"
                    :max="maximo"
                    full-width
                    @change="getDolar(fecha)"
                    locale="es-cl" color="blue darken-2"></v-date-picker>
                </v-row>
                <!--cuando agregamos v-model trabajamos con data   -->
         
             
          


        </v-row>
    </div>
</template>

<script>

import axios from "axios";      //llamos a axios y le ponemos el mismo nombre
import {mapMutations} from "vuex";

export default {
    data() {
        return {
            fecha: new Date().toISOString().substr(0, 10),      //trae la fecha de hoy || se trae una nueva fecha-en formato de string-y esta substrayendo los primero 3 nuemoros
            minimo: '1984',                                     //1984 fecha minima
            maximo: new Date().toISOString().substr(0, 10),     //trae la fecha de hoy || se trae una nueva fecha-en formato de string-y esta substrayendo los primero 3 nuemoros
            valor: null,                                        //nulo mientras llega variable
            
        }
    },
    methods: {

        ...mapMutations(['mostrarLoading','ocultarLoading']), //ahora podemos usar estas mutaciones
        // los 3 puntitos puedes mostrar mas de una metodo

        async getDolar(dia) {    //funcion asincrona--nuevafuncion js - para trabajar con await
            
            let arrayFecha = dia.split(['-'])          //toma la fecha y la rompe convirtiendola en un array
            let ddmmyy = arrayFecha[2]+'-'+arrayFecha[1]+'-'+arrayFecha[0];
            //toma la posicion de los array y por medio de concatenacion los ordena                  
          
            try{

                this.mostrarLoading({titulo:'Espera Porfavor', color:'secondary'})
                   

                let datos = await axios.get(`https://mindicador.cl/api/dolar/${ddmmyy}`)
                //await--va a esperar a axios
            
                if(datos.data.serie.length > 0) {
                    this.valor = await datos.data.serie[0].valor
                    console.log(this.valor) 
                }else {
                    this.valor = 'Sin resultados'
                }
            

        
            } 
            
            catch(error) {
                    console.log(error);
            }
              finally{
                this.ocultarLoading()
            }
        }
        
    },
    created(){ //cuando se cree nuestro documento
        this.getDolar(this.fecha); // llamamos la funcion get dolar
    }
}

</script>



